import React, { Fragment, useState } from 'react';
import { Link } from 'gatsby';
import { Styled, css } from 'theme-ui';
import Select from 'react-select';

import Layout from 'gatsby-theme-blog/src/components/layout';
import SEO from 'gatsby-theme-blog/src/components/seo';
import Footer from 'gatsby-theme-blog/src/components/home-footer';

const options = [
  { value: 'all', label: 'Tất cả' },
  { value: 'JS', label: 'JS' },
  { value: 'CSS', label: 'CSS' },
  { value: 'React', label: 'React' },
];

const dateOptions = {
  weekday: 'long',
  year: 'numeric',
  month: 'long',
  day: 'numeric',
};

const colourStyles = {
  option: (styles) => {
    return {
      ...styles,
      color: 'black'
    };
  },
};

const Posts = ({ location, posts: originalPosts, siteTitle, socialLinks }) => {
  const [tag, setTag] = useState(options[0]);

  const posts = originalPosts.filter(post => {
    if (tag.value === 'all') {
      return true;
    } else {
      return post.node.tags.includes(tag.value);
    }
  }).map(post => ({
    ...post,
    node: {
      ...post.node,
      date: new Date(post.node.date).toLocaleDateString('vi-VN', dateOptions),
    },
  }));

  return (
    <Layout location={location} title={siteTitle}>
      <Styled.h2
        css={css({
          mb: 2,
        })}
      >
        Chọn chủ đề đọc nè
      </Styled.h2>
      <Select value={tag} onChange={setTag} options={options} styles={colourStyles} />
      <div style={{ padding: 20 }}></div>
      <main>
        {posts.map(({ node }) => {
          const title = node.title || node.slug;
          const keywords = node.keywords || [];
          return (
            <Fragment key={node.slug}>
              <SEO title="Tất cả bài đăng" keywords={keywords} />
              <div>
                <Styled.h2
                  css={css({
                    mb: 1,
                  })}
                >
                  <Styled.a
                    as={Link}
                    css={css({
                      textDecoration: `none`,
                    })}
                    to={node.slug}
                  >
                    {title}
                  </Styled.a>
                </Styled.h2>
                <small>{node.date}</small>
                <Styled.p>{node.excerpt}</Styled.p>
              </div>
            </Fragment>
          );
        })}
      </main>
      <Footer socialLinks={socialLinks} />
    </Layout>
  );
};

export default Posts;
